<template>
  <div class="main-div">
    <div class="main-bg">
      <div class="contain">
        <div class="firstItem"></div>
        <div class="secondItem"></div>
        <div class="thirdItem"></div>
        <div class="description-contain">
          <p class="description">博物館已經偵測出結界的三個端點。請站在『定位點』上，調整羅盤直到它正確呈現這三個地點的方位，破解結界的密碼就會顯現，即可進入結界。請拿出道具包的「怪奇羅盤」定位。</p>
        </div>
        <p id="answer-title">請拿出道具包的「怪奇羅盤」定位。</p>
        <div id="answer"><input style="padding: 4px;" type="text" v-model="answer"></div>
        <div id="button-contain"><div id="button" @click="submitPressed"></div></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommuteAnswerCompassPosition',
  data () {
    return {
      answer: ''
    }
  },
  mounted () {
    this.$store.commit('changeHeader', true)
  },
  methods: {
    submitPressed () {
      const lowerCaseAnswer = this.answer.trim().toLowerCase()
      if (lowerCaseAnswer === 'ngx') {
        this.$router.push({name: 'CommuteMapEntrance'})
      } else {
        alert('答錯囉！')
        console.log('wrong')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/map-greenlight.png');
      position: relative;
      .contain {
        position: absolute;
        width: 100%;
        top: $topSpace;
        .firstItem {
          @extend answerCompassImage;
          background-image: url('~@/assets/images/commute/KEELUNG-land-mark.png');
        }
        .secondItem {
          @extend answerCompassImage;
          background-image: url('~@/assets/images/commute/zhong-zheng-park.png');
        }
        .thirdItem {
          @extend answerCompassImage;
          background-image: url('~@/assets/images/commute/lion-ball-fort.png');
        }
        .description-contain {
          .description {
            @extend descriptionWhiteText;
            margin: 10px 16px;
          }
        }
        #answer-title {
          color: $greenColor;
          text-align: center;
        }
        #answer {
          text-align: center;
        }
        #button-contain {
          margin-top: 10px;
          text-align: center;
          #button {
            display: inline-block;
            width: 50px;
            height: 50px;
            background-image: url('~@/assets/images/go-right-arrow.png');
            background-size: contain;
          }
        }
      }
    }
  }
</style>



